@import url(https://fonts.googleapis.com/css2?family=Courgette&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
.nav-title {
    font-size: 24px;
    font-weight: 700;
    display: inline-block;
    color: #f2f2f2;
    padding-left: 20px;
    padding-right: 20px;
}
.nav-title:hover {
    background-color: #551d1d;
    box-shadow: inset 0 0 25px #160505;
}

.nav {
  /* overflow: hidden; */
  background-color: #3a1212;
  /* vertical-align: middle; */
  display: flex;
  box-shadow: 0px 8px 20px #000000;
}

.nav-link {
  /* float: left; */
  /* display: flex; */
  color: #f2f2f2;
  /* background-color: #3a1212; */
  /* text-align: right; */
  padding: 24px 10px;
  text-decoration: none;
  font-size: 15px;
  font-family: 'Courgette';
  /* these last two make the logout button in
  the nav bar look like another link */
  border: none;
  cursor: pointer;
  outline: 0;
}

.nav-link:hover {
  background-color: #551d1d;
  box-shadow: inset 0 0 25px #160505;
}

.nav-right {
  align-self: center;
  position: absolute;
  right: 25px;
}
footer {
    margin-top: 80px;
}

/* @import url('https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap'); */

body {
    background-image: url(/static/media/background2.b74f1b06.jpg);
    font-family: 'Courgette', cursive, 'Segoe UI', Tahoma;
    margin: 0px;
}

.alert {
    background-color: #ff4545;
    margin: 50px;
}

form {
    background-color: #dbd5d5;
    width: 30%;
    height: 250px;
    margin: 20px auto;
    padding: 25px;
    border-radius: 2px;
}

input {
    margin: 5px;
    margin-bottom: 10px;
}

.log-in, .log-out, .register {
  background-color: #3a1212;
  border-radius: 4px;
  color: #f8f8f8;
  min-height: 50px;
  min-width: 100px;
  outline:0;
  cursor: pointer;
}

.log-in:hover, .log-out:hover, .register:hover {
  background-color: #551d1d;
}

/* This line makes the "Register" and "Login" buttons look like links */
.link-button {
    background:none;
    color:inherit;
    border:none;
    padding:0;
    font: inherit;
    outline:0;
    border-bottom:1px solid #444; 
    cursor: pointer;
}

